.form {
  @media (min-width: $tablet) {
    display: grid;
    grid-template-columns: 1;
  }

  @media (min-width: $desktop) {
    grid-template-columns: 1fr 1fr;
    max-width: 940px;
    margin: 0 auto;
  }
}

.form__purpose {
  @include mobile-text;
  line-height: 30px;
  text-align: center;
  margin: 0 auto;
  padding: 54px 20px;

  @media (min-width: $tablet) {
    @include desctop-text;
    width: 490px;
    order: 1;
  }

  @media (min-width: $desktop) {
    width: 756px;
    grid-column: span 2;
    padding-bottom: 89px;
  }
}

.form__purpose::after {
  @media (min-width: $tablet) {
    content: " Пожалуйста, заполните форму ниже:";
  }
}

.form__user-name {
  padding: 0 20px;

  @media (min-width: $tablet) {
    order: 2;
  }

  @media (min-width: $desktop) {
    grid-row-start: 2;
    grid-row-end: 3;
    padding: 0 35px 0 0;
  }
}

.form__filter {
  @media (min-width: $tablet) {
    order: 5;
    margin: 0 20px;
  }

  @media (min-width: $desktop) {
    grid-column: span 2;
    margin: 0;
  }
}

.form__user-contacts {
  @media (min-width: $tablet) {
    order: 4;
    margin: 0 20px;
  }

  @media (min-width: $desktop) {
    grid-column: span 2;
    margin: 0;
  }
}

.form__radio {
  @media (min-width: $tablet) {
    order: 3;
  }

  @media (min-width: $desktop) {
    grid-row-start: 2;
    grid-row-end: 3;
    justify-self: end;
  }
}

.form__field {
  margin: 42px 22px 0 22px;

  @media (min-width: $tablet) {
    order: 6;
  }

  @media (min-width: $desktop) {
    grid-column: span 2;
    margin: 69px 0 0 0;
  }
}

.form__submit {
  @media (min-width: $tablet) {
    order: 7;
  }

  @media (min-width: $desktop) {
    grid-column: span 2;
  }
}
