.navigation {
  display: block;
}

.navigation__toggle {
  display: block;
  position: relative;
  width: 50px;
  height: 24px;
  cursor: pointer;
  z-index: 10;
}
@media (min-width: 1200px) {
  .navigation__toggle {
    display: none;
  }
}

.navigation__toggle span {
  position: absolute;
  background-color: white;
  width: 100%;
  height: 4px;
  top: 10px;
  border-radius: 1px;
  transition: all 0.4s ease 0s;
}
.navigation__toggle span:hover {
  background-color: #d22855;
}
.navigation__toggle span:active {
  background-color: rgba(210, 40, 85, 0.3);
}

.navigation__toggle::before,
.navigation__toggle::after {
  content: "";
  background-color: white;
  position: absolute;
  width: 100%;
  height: 4px;
  left: 0;
  border-radius: 1px;
  transition: all 0.4s ease 0s;
}

.navigation__toggle:hover::before,
.navigation__toggle:hover::after {
  background-color: #d22855;
}

.navigation__toggle:active::before,
.navigation__toggle:active::after {
  background-color: rgba(210, 40, 85, 0.3);
}

.navigation__toggle::before {
  top: 0;
}

.navigation__toggle::after {
  bottom: 0;
}

.navigation__toggle.active::before {
  transform: rotate(45deg);
  top: 10px;
  left: 13px;
  width: 23px;
}

.navigation__toggle.active::after {
  transform: rotate(-45deg);
  bottom: 10px;
  left: 13px;
  width: 23px;
}

.navigation__toggle.active span {
  transform: scale(0);
}

